import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

class CheckBox extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isChecked: false
    };
    this.handleChecked = this.handleChecked.bind(this);
  }

  getWordCount = () => {
    // console.log(this.props.text)
    const plainText = this.props.text;
    const regex = /(?:\r\n|\r|\n)/g; // new line, carriage return, line feed
    const cleanString = plainText.replace(regex, " ").trim(); // replace above characters w/ space
    const wordArray = cleanString.match(/\S+/g); // matches words according to whitespace

    return wordArray ? wordArray.length : 0;
  };

  sentenceNumber = (name) => {
    const number = name.split('-')[1];
    return number;
  }

  handleChecked = () => {
    this.setState({isChecked: !this.state.isChecked});
    const nWord = this.getWordCount();

    // console.log(this.state.isChecked, nWord)

    if (this.props.handleNumWord) {
      if (this.state.isChecked) {
        this.props.handleNumWord({
          category: 'CheckBox',
          action: 'removeSelection',
          sent: this.sentenceNumber(this.props.name) *1, //this.props.name,
          value: -nWord,
          text: this.props.text
        });
      } else {
        this.props.handleNumWord({
          category: 'CheckBox',
          action: 'addSelection',
          sent: this.sentenceNumber(this.props.name) *1, //this.props.name,
          value: nWord,
          text: this.props.text
        });
      }


    }
  };



  render() {
    return (
      <span className="segmentCheckbox">
        <input
          type="checkbox"
          id={this.props.name} name={this.props.name} value={this.props.name}
          onChange={this.handleChecked}
          style={{ marginRight: "8px", verticalAlign: "middle" }}/>
        <label for={this.props.name}
          style={{ fontSize: "13px" }}>{this.props.name}</label>
      </span>
    );
  }
}

CheckBox.propTypes = {
  name: PropTypes.string,
  // handleOnChange: PropTypes.func,
  text: PropTypes.string,
  handleNumWord: PropTypes.func,
};

export default CheckBox;
