  render() {
    let startTimecode = this.state.start;
    if (this.props.blockProps.timecodeOffset) {
      startTimecode += this.props.blockProps.timecodeOffset;
    }
    const timecodeElement = (
      <span className={ style.time } onClick={ this.handleTimecodeClick }>
        {shortTimecode(startTimecode)}
      </span>
    );

    const sentenceLabel = () => {
      const speaker = this.props.block.getData().get('speaker') || ""
      const suffix = speaker.split(' ')[1];
      return `sentence-${suffix}`;
    }

// handleOnChange={this.handleChecked}
    return (
      <div className={ style.WrapperBlock }>
        <div className={ [ style.markers, style.unselectable ].join(' ') } contentEditable={ false }>
          <CheckBox
            name={`${sentenceLabel()}`}
            text={this.props.block.text}
            handleNumWord={this.props.blockProps.handleNumWord}
          />
          {this.props.blockProps.showTimecodes ? timecodeElement : ''}
        </div>
        <div className={ style.text }>
          {this.props.block.text}
        </div>
      </div>
    );
  }
