const DEMO_TRANSCRIPT = "https://raw.githubusercontent.com/sangwoo3/html_files/master/transcripts/transcript_v0346_s21.json"
const DEMO_MEDIA_URL = "http://streamprod-eastasia-streamprodeastasia-aaea.streaming.media.azure.net/4d06c3cd-4b12-4026-9eff-125f55d0f060/output.mp4";
const DEMO_TITLE = "Photo Manipulation: Willy and Friends" // "TED Talk | Kate Darling - Why we have an emotional connection to robots";


class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      transcriptData: null,
      mediaUrl: null,
      isTextEditable: false,
      spellCheck: false,
      sttType: "google-stt",//"bbckaldi",
      analyticsEvents: [],
      title: "",
      fileName: "",
      autoSaveData: {},
      autoSaveContentType: "draftjs",
      autoSaveExtension: "json"
    };

    this.transcriptEditorRef = React.createRef();
  }

  componentDidMount () {
    this.loadVideo();
  }


  loadVideo = () => {
    const transcriptData_ = document.getElementById("transcriptData").value;//this.transcriptDataInput.current;
    const mediaUrl_       = document.getElementById("mediaUrl").value;//this.mediaUrlInput.current;
    const title_          = document.getElementById("title").value;//this.titleInput.current;
    // console.log(transcriptData_.value, mediaUrl_.value, title_.value)

    // const transcriptData_ =  DEMO_TRANSCRIPT
    // const mediaUrl_ = DEMO_MEDIA_URL
    // const title_ = DEMO_TITLE
    // console.log(transcriptData_, mediaUrl_, title_)


    fetch(transcriptData_) // .value
      .then(res => res.json())
      .then((data) => {
        this.setState({ transcriptData: data })
      })
      .catch(console.log)

    this.setState({
       mediaUrl: mediaUrl_, //.value
       title: title_,       //.value
       sttType: "google-stt" //"bbckaldi"
     });

  };

  handleAnalyticsEvents = event => {
    this.setState({ analyticsEvents: [...this.state.analyticsEvents, event] });
  };

  // inside return
  // <section style={{ height: "250px", width: "50%", float: "left" }}>
  //   <h3>Components Analytics</h3>
  //   <textarea
  //     style={{ height: "100%", width: "100%" }}
  //     value={JSON.stringify(this.state.analyticsEvents, null, 2)}
  //     disabled
  //   />
  // </section>

  render () {
    return (
      <div className="Summary_annotation">
        <TranscriptEditor
          transcriptData={this.state.transcriptData}
          fileName={this.state.fileName}
          mediaUrl={this.state.mediaUrl}
          isEditable={this.state.isTextEditable}
          spellCheck={this.state.spellCheck}
          sttJsonType={this.state.sttType}
          handleAnalyticsEvents={this.handleAnalyticsEvents}
          title={this.state.title}
          ref={this.transcriptEditorRef}
          handleAutoSaveChanges={this.handleAutoSaveChanges}
          autoSaveContentType={this.state.autoSaveContentType}
          mediaType={ 'video' }
        />

      </div>
    );
  }

}

export default App;
