import React from 'react';
import Warper from './Warper';
import Popup from "reactjs-popup";

const contentStyle = {
  maxWidth: '1200px',
  width: '90%',
};

const CustomModal = () => (
  <Popup
    trigger={<button type="button" className="button">View Instructions</button> }
    modal
    lockScroll={false}
    contentStyle={contentStyle}>
    {close => (
      <div className="modal">
        <a className="close" onClick={close}>
          &times;
        </a>
        <div className="content">
          <div class="col-12" id="actualInstructions">
            <h2>Tasks</h2>
            <dl>
              <dt>Task A - Select sentences that best summarize a 5 minute video</dt>
              <p>👉<em>Read</em> the whole transcript and <em>select</em> sentences that represent the main idea of the transcript as best as possible.</p>
              <dd>
                <ol>
                  <li>Select <em>at least ONE</em> sentence from the transcript. <em>Choosing 3 to 7 sentences</em> (or more if necessary) is recommended.</li>
                  <li>The selected sentences should be closely related to your written summary in Task B.</li>
                </ol>
              </dd>

              <dt>Task B - Write a short summary in your own words</dt>
              <dd>👉<em>Write</em> a short summary in your own words (1~2 sentences recommended) in the summary box based on your selected sentences.</dd>
            </dl>

            <h2>Notes</h2>
              <ol>
                  <li>
                    The title of the video is shown on the top of the transcript.
                  </li>
                  <li>
                    We only care about the transcribed parts of the video. If the video is longer that 5 minutes, you should do your tasks based on the shown transcript and corresponding video content.
                  </li>
                  <li>
                    You do NOT need to watch the entire 5 minute long video if you can do your task by only reading the transcript. The video is to help you understand the context of the transcript.
                  </li>
              </ol>

            <h2>Caveats</h2>
              <ul>
                  <li>
                    Some videos are not available. In that case, please do the tasks based only on the transcripts.
                  </li>
                  <li>
                    Please skip the tasks if the transcripts are not related to the video content (and let us know).
                  </li>
              </ul>

            <h2>Video Player Control</h2>
              <ul>
                  <li>
                      By click a timestamp, you can jump to that exact time in the video, which is provided to help you understand the context.
                  </li>
                  <li>
                    Clicking the video area enables play or pause the video.
                  </li>
              </ul>
          </div>
        </div>
      </div>
    )}
  </Popup>
);

export default Warper(CustomModal);
